<style>
  .p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }

  .img-w {
    width: 80px;
    height: 80px;
  }

  .img-w>img {
    width: 80px;
  }

  .p-m-c-r {
    padding: 0 10px;
  }

  .erp-p-no {
    color: #666;
    font-size: 12px;
  }

  .order-id {
    cursor: pointer;
    color: #2077FF;
  }
  .order-id:hover {
    color: #409EFF;
  }

</style>

<template>
  <div>
    <!-- 搜索框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <region @RegionChange="RegionChange" class="region-wrap"></region>
        <el-form-item label="订单来源">
          <el-select class="search-input" v-model="searchContent.platformId" placeholder="请选择订单来源"
            size="small" @change="doSearchHandel(true)">
            <el-option :label="item.value" :value="item.code" :key="item.code" v-for="item in platformIdOptions">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务类型">
          <el-select class="search-input" v-model="searchContent.businessType" placeholder="请选择业务类型" size="small"
            @change="doSearchHandel(true)">
            <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in businessTypeOptions">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="下单时间">
          <date-picker-range 
            :defaultRange="searchContent.defaultRange"
            @datePickerRangeChange="datePickerRangeChange">
          </date-picker-range>
        </el-form-item>
        <el-form-item label="开始提货日期">
          <el-date-picker class="search-input" v-model="searchContent.pickUpTime" type="date"
            placeholder="请选择下单结束时间" format="yyyy-MM-dd" value-format="yyyy-MM-dd" size="small"
            @change="doSearchHandel(true)">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="SKU编码/商品名称">
          <el-input class="search-input" v-model.trim="searchContent.searchValue" placeholder="SKU编码/商品名称" size="small"
            @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="是否顺丰仓现货">
          <el-select class="search-input" v-model="searchContent.sfInStock" filterable placeholder="请选择状态" size="small" @change="doSearchHandel(true)" clearable>
            <el-option
              v-for="item in sfInStockOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="供应商专供">
          <el-select class="search-input" v-model="searchContent.vendorCode" filterable placeholder="供应商专供" size="small" @change="doSearchHandel(true)" clearable>
            <el-option
              v-for="item in vendorList"
              :key="item.vendorNo"
              :label="item.vendorName"
              :value="item.vendorNo">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          <el-button @click="exportData" size="mini" :loading="exportLoading">导出</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table class="fixed-height" v-loading="loading" :data="tableData" empty-text="没有查询到您想要的数据" size="small" @sort-change="sortChange" show-summary :summary-method="getSummaries">
        <el-table-column min-width="80px" prop="regionName" label="区域"></el-table-column>
        <el-table-column label="商品信息" min-width="236px">
          <template slot-scope="scope">
            <div class="p-m-w">
              <div class="img-w">
                <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt=""
                  onerror="onerror=null;src='/shop/static/img_error.png'">
                <img v-else src="/shop/static/img_error.png" alt="">
              </div>
              <div class="p-m-c-r">
                <div>{{scope.row.goodsName}}</div>
                <div class="erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                <el-tag type="warning" size="mini" v-if="scope.row.vendorName">{{scope.row.vendorName}}</el-tag>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="90px" prop="spec" label="规格"></el-table-column>
        <el-table-column min-width="90px" prop="businessTypeStr" label="业务类型" :formatter="emptyFormatter"></el-table-column>
        <el-table-column min-width="90px" label="销售量" prop="quantity" sortable="custom">
          <template slot-scope="scope" size="small">
            <div class="order-id" @click="goSearchRes(scope.row, 'QUANTITY')">
              {{scope.row.quantity}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="120px" prop="amount" label="销售额" sortable="custom"></el-table-column>
        <el-table-column min-width="90px" prop="summarizeQty" v-if="searchContent.platformId !== 5" :formatter="emptyFormatter" :render-header="summarizeQtyHeader"></el-table-column>
        <el-table-column min-width="90px" prop="receiveQty" v-if="searchContent.platformId !== 5" :formatter="emptyFormatter" :render-header="receiveQtyHeader"></el-table-column>
        <el-table-column min-width="90px" label="已核销" prop="completeQty" sortable="custom"> 
          <template slot-scope="scope" size="small">
            <div class="order-id" @click="goSearchRes(scope.row, 'COMPLETEQTY')">
              {{scope.row.completeQty}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="90px" label="已退" prop="refundQty" sortable="custom">
          <template slot-scope="scope" size="small">
            <div class="order-id" @click="goSearchRes(scope.row, 'REFUNDQTY')">
              {{scope.row.refundQty}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="90px" label="待核销" prop="awaitQty" sortable="custom">
          <template slot-scope="scope" size="small">
            <div class="order-id" @click="goSearchRes(scope.row, 'AWAITQTY')">
              {{scope.row.awaitQty}}
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination v-if="tableData.length>0" class="tc mt20" background @size-change="changeSize"
        @current-change="changeCurrent" :current-page.sync="pageNum" :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import config from '@/utils/config'
  import exportSheet from '@/utils/exportExcel'
  import * as utils from '@/utils/utils'
  import DatePickerRange from '@/components/Common/DatePickerRange'
  import Region from '@/components/Common/StoreCommon/Region';
  const areaPreGoodsSumUrl = {
    // 区域商品汇总
    areaSummary: '/plateform/a/order/city/summary',
    // 区域商品汇总导出
    exportData: '/plateform/a/order/city/export',
    // 获取供应商编码及名称（编码[vendorCode]，名称[vendorName]）
    vendorlist: '/newretail-shop/a/order/vendorlist'
  }
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        exportLoading: false,
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          DIFFCITY: true,
          platformId: 5, // 订单来源
          defaultRange: [],
          startPayTime: '', // 开始时间
          endPayTime: '', // 结束时间
          pickUpTime: utils.timestampToTimeNoHMS(Date.parse(new Date()) + 86400000), // 开始提货日期
          // ERP编码、商品名称
          searchValue: '',
          // 1：常规；2：预售；3:长周期预售，4：及时达
          businessType: '',
          regionCode: Storage.get('regionCode') || '',
          sfInStock: '',
          vendorCode: ''
        },
        tableData: [],
        exportConfig: {
          regionName: '区域',
          goodsNo: 'SKU编码',
          skuNo: 'ERP编码',
          goodsName: '商品名称',
          spec: '商品规格',
          businessTypeStr: '业务类型',
          quantity: '销售量',
          amount: '销售额',
          completeQty: '已核销',
          refundQty: '已退',
          awaitQty: '待核销'
        },
        businessTypeOptions: [{
            label: '全部',
            value: ''
          },
          {
            label: '预售',
            value: 3
          },
          // {
          //   label: '短周期预售',
          //   value: 2
          // },
          {
            label: '及时达',
            value: 4
          },
          {
            label: '拼团',
            value: 5
          },
          {
            label: '虚拟订单',
            value: 6
          }
        ],
        // 订单来源下拉框
        platformIdOptions: [
          {
            code: 5,
            value: '本来鲜'
          },
          {
            code: 6,
            value: '顺丰优选'
          }
        ],
        totalSum: {},
        regionCode: Storage.get('regionCode') || '',
        sfInStockOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '是',
            value: true
          },
          {
            label: '否',
            value: false
          }
        ],
        vendorList: []
      }
    },
    mounted() {
      this.getVendorlist()
      let searchContentMsg = Storage.get("searchContent")
      if (this.$route.meta.keepAlive && searchContentMsg) {
        this.pageNum = searchContentMsg.pageNum
        this.searchContent = searchContentMsg.searchContent
        Storage.remove('searchContent')
      }
      this.doSearchHandel()
    },
    components: {
      "date-picker-range": DatePickerRange,
      "region": Region
    },
    methods: {
      RegionChange(param) {
        this.regionCode = param.regionCode
        this.searchContent.regionCode = this.regionCode
      },
      emptyFormatter(row, column, cellValue) {
        if(cellValue || cellValue == 0) {
          return cellValue
        }else {
          return '--'
        }
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize(value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent(value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 获取订单列表内容]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        let startPayTimeVal = this.searchContent.startPayTime ? new Date(this.searchContent.startPayTime.replace(/-/g,
          '/')).getTime() : 0
        let endPayVal = this.searchContent.endPayTime ? new Date(this.searchContent.endPayTime.replace(/-/g, '/'))
          .getTime() : 0

        if (this.searchContent.startPayTime && this.searchContent.endPayTime && !isNaN(startPayTimeVal) && !isNaN(
            endPayVal)) {
          if (startPayTimeVal > endPayVal) {
            this.$message("结束日期不能早于开始日期")
            return
          }
        }
        this.loading = true
        if (_param === true) {
          this.pageNum = 1
        }
        this.$http.post(areaPreGoodsSumUrl.areaSummary, {
          ...this.searchContent,
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }).then(res => {
          this.loading = false
          if (res.code === 0) {
            if (res.data && res.data.result && res.data.result.length) {
              this.tableData = res.data.result
            } else {
              this.tableData = []
            }
            this.totalSum = {...res.data.summary}
            this.total = res.data.total
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [exportData 导出数据]
       * @return {[type]} [description]
       */
      exportData() {
        let startPayTimeVal = this.searchContent.startPayTime ? new Date(this.searchContent.startPayTime.replace(/-/g,
          '/')).getTime() : 0
        let endPayVal = this.searchContent.endPayTime ? new Date(this.searchContent.endPayTime.replace(/-/g, '/'))
          .getTime() : 0

        if (this.searchContent.startPayTime && this.searchContent.endPayTime && !isNaN(startPayTimeVal) && !isNaN(
            endPayVal)) {
          if (startPayTimeVal > endPayVal) {
            this.$message("结束日期不能早于开始日期")
            return
          }
        }
        this.exportLoading = true
        this.$http.post(areaPreGoodsSumUrl.exportData, {
          ...this.searchContent
        }).then(res => {
          this.exportLoading = false
          if (res.code === 0) {
            if (res.data && res.data.length) {
              let tableDataArr = res.data
              const tHeader = utils.objectToArray(this.exportConfig, false)
              const filterVal = utils.objectToArray(this.exportConfig, true)
              exportSheet.ExportJsonToExcel({
                header: tHeader,
                filterVal,
                data: tableDataArr,
                filename: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))}_区域商品汇总`
              })
              setTimeout(() => {
                this.$message.success("导出成功")
              }, 500)
            } else {
              this.$notify.warning({
                title: '温馨提示',
                message: '没有查询到要导出的数据'
              });
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.exportLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      summarizeQtyHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover"
            content="说明：已订货为昨晚线上汇总提交的订货数量，正常等于销售量，如果为0则为异常，提交订货失败。">
            <span slot="reference">已订货<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      receiveQtyHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover"
            content="说明：已收货为开始提货日期当日门店线上线下收货总数量，已转化成线上份数；正常应该大于销售量，小于则异常，缺量导致订单无法正常履约。">
            <span slot="reference">已收货<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      sortChange(column) {
        let order = ''
        if(column.order === 'ascending') {
          order = 'asc'
        }else if(column.order === 'descending') {
          order = 'desc'
        }
        
        if(column.prop && order) {
          let orderBy = `${column.prop} ${order}`
          this.searchContent.orderBy = orderBy
          this.doSearchHandel(true)
        }
      },
      goSearchRes(_row, _type) {
        let _paramMsg = {
          from: 'areaSum',
          pageNum: this.pageNum,
          searchContent: this.searchContent
        }
        Storage.set("searchContent", _paramMsg)
        let routerPath = `/search_result?from=areaSum&searchType=${_type}&searchValue=${_row.goodsNo}`
        this.$router.push(routerPath)
      },
      /**
       * @description: 下单时间范围发生变化
       * @param {type} 
       * @return: 
       */
      datePickerRangeChange(result) {
        this.searchContent.defaultRange = result ? [...result] : result
        this.searchContent.startPayTime = result ? result[0] : ''
        this.searchContent.endPayTime = result ? result[1] : ''
        this.doSearchHandel(true)
      },
      /**
       * @description: 显示总计
       * @param {type} 
       * @return: 
       */
      getSummaries(params) {
        let sums = []
        let newArray=[]
        let selectedColumn = params.columns
        selectedColumn.forEach(item => {
          if(this.totalSum[item.property] || this.totalSum[item.property] === 0) {
            newArray.push(this.totalSum[item.property])
          }else {
            newArray.push('')
          }
        })
        sums = newArray;
        sums[0] = "合计";
        return sums
      },
      getVendorlist() {
        this.$http.post(areaPreGoodsSumUrl.vendorlist, {regionCode: this.searchContent.regionCode, DIFFCITY: this.searchContent.DIFFCITY}).then(res => {
          if(res.code === 0) {
            this.vendorList = res.data && res.data.length ? [{vendorNo: '', vendorName: '全部'}, ...res.data] : []
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("获取供应商编码及名称（编码[vendorCode]，名称[vendorName]）报错：", e.message);
        })
      }
    }
  }

</script>



